<template>
  <div class="confirmOrder">
    <div class="mescroll">
       <div class="top">
      <div class="title">确认订单</div>
      <div class="address">
        <div class="label">我的收货地址</div>
        <div class="value" @click="$router.push({name:'myAddress',query:{type:1}})">
          <img class="icon" src="@/assets/img/merchantsIcon/address1.png" alt />
          <div v-if="address.id">
            <p class="name">
              收货人:{{address.userName2}}
              <span>{{address.userMobile}}</span>
            </p>
            <p class="addr">收货地址:{{(address.area.treeNames?address.area.treeNames.replace(/\//g,''):'') + address.addrAddr}}</p>
          </div>
          <div v-else>
            <p class="text">请选择收货地址</p>
          </div>
          <img class="icon1" src="@/assets/img/merchantsIcon/right1.png" alt />
        </div>
      </div>
    </div>
    <div class="mid">
      <div class="name">
        <span>订单明细</span>
        <span class="text">(共{{num}}个商品)</span>
      </div>
      <div class="list">
        <div class="item" v-for="(item,index) in goodList" :key="index">
          <div class="shopName">{{item.gname}}</div>
          <div class="num">×{{item.num}}</div>
          <div class="price">￥{{item.goutPrice.toFixed(2)}}</div>
        </div>
      </div>
      <div class="allPrice">
        <span>合计:</span>
        <span style="font-weight:bold;">￥</span>
        <span style="font-size:20px;font-weight:bold;">{{allPrice.toFixed(2)}}</span>
      </div>
      <div class="remark">
        <div class="text">备注：</div>
        <div class="value">
          <van-field v-model="remark" rows="2" type="textarea" maxlength="30" placeholder="可备注信息，如几点取餐，偏爱口味等" show-word-limit @blur="blur()" />
        </div>
      </div>
    </div>
    </div>
   
    <div class="bottom">
      <div class="flex">
        <div class="allPrice">
          <span>合计:</span>
          <span style="font-weight:bold;">￥</span>
          <span style="font-size:20px;font-weight:bold;">{{allPrice.toFixed(2)}}</span>
        </div>
        <div class="right">
          <div class="btn" @click="submit">提交订单</div>
        </div>
      </div>
    </div>
    <van-popup v-model="popupShow">
      <div class="popup_wrap">
        <div class="popup_icon" @click="popupShow = false">
          <img src="../../assets/img/icon/cha-3.png" />
        </div>
        <div class="popup_content">
          <div class="title">
            支付
            <span>{{text}}！</span>
          </div>
          <div class="text">您可以在15分钟内前往我的订单进行支付，超过时长的订单将失效</div>
        </div>
        <div class="popup_btns">
          <div class="popup_btn" @click="cancel">取消</div>
          <div class="popup_btn go" @click="go">立即前往</div>
        </div>
      </div>
    </van-popup>
  </div>
</template>
<script>
import { Popup, Field } from "vant";
import { getAddressList, submitOrder1 } from "@/api";
export default {
  components: {
    [Popup.name]: Popup,
    [Field.name]: Field
  },
  data() {
    return {
      address: {},
      goodList: [],
      num: 0,
      allPrice: 0,
      remark: "",
      popupShow: false,
      text: "失败"
    };
  },
  methods: {
    blur() {
      document.body.scrollTop = 0;
    },
    submit() {
      if (!this.address.id) {
        this.$toast("请选择地址");
        return;
      }
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      let list = [];
      this.goodList.map(item => {
        list.push({
          gcode: item.id,
          gorderCount: item.num
        });
      });
      submitOrder1({
        shopCode: this.$route.query.shopCode,
        addressId: this.address.id,
        payType: "1",
        orderDetailParamsList: list,
        remarks: this.remark
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.orderId = res.data.orderId;
          if(res.data.appId){
            this.weChatPay(
              res.data.appId,
              res.data.timeStamp,
              res.data.nonceStr,
              res.data.package,
              res.data.signType,
              res.data.paySign,
              res.data.orderId,
              res.data.orderNo,
              res.data.oorderWdate
            );
          }else{
            this.$router.replace({
              name: "payResult",
              query: {
                orderId:  res.data.orderId,
                orderNo: res.data.orderNo,
                payPrice: this.allPrice,
                payTime: res.data.oorderWdate
              }
            });
          }
        } else {
          this.$toast(res.msg);
        }
      });
    },
    weChatPay(
      appId,
      timeStamp,
      nonceStr,
      packageValue,
      signType,
      sign,
      orderId,
      orderNo,
      payTime
    ) {
      // console.log(appId, timeStamp, nonceStr, packageValue, signType, sign);
      var $this = this;
      if (typeof WeixinJSBridge == "undefined") {
        if (document.addEventListener) {
          document.addEventListener(
            "WeixinJSBridgeReady",
            onBridgeReady,
            false
          );
        } else if (document.attachEvent) {
          document.attachEvent("WeixinJSBridgeReady", onBridgeReady);
          document.attachEvent("onWeixinJSBridgeReady", onBridgeReady);
        }
      } else {
        onBridgeReady();
      }
      function onBridgeReady() {
        WeixinJSBridge.invoke(
          "getBrandWCPayRequest",
          {
            appId: appId, //公众号名称，由商户传入
            timeStamp: timeStamp, //时间戳，自1970年以来的秒数
            nonceStr: nonceStr, //随机串
            package: packageValue,
            signType: signType, //微信签名方式：
            paySign: sign //微信签名
          },
          function(res) {
            console.log(res);
            if (res.err_msg == "get_brand_wcpay_request:ok") {
              $this.$toast("支付成功", "middle");
              setTimeout(() => {
                $this.$router.replace({
                  name: "payResult",
                  query: {
                    orderId: orderId,
                    orderNo: orderNo,
                    payPrice: $this.allPrice,
                    payTime: payTime
                  }
                });
              }, 500);
            } else if (res.err_msg == "get_brand_wcpay_request:cancel") {
              $this.show = false;
              $this.popupShow = true;
              $this.text = "取消";
              console.log("支付取消");
            } else if (res.err_msg == "get_brand_wcpay_request:fail") {
              $this.show = false;
              $this.popupShow = true;
              $this.text = "失败";
              // $this.$toast("支付失败，请稍后再试", "middle");
            }
            // 使用以上方式判断前端返回,微信团队郑重提示：res.err_msg将在用户支付成功后返回    ok，但并不保证它绝对可靠。
          }
        );
      }
    },
    cancel() {
      this.$router.back();
    },
    go() {
      this.$router.replace({
        name: "orderDetail",
        query: { id: this.orderId }
      });
    },
    loadData() {
      if (sessionStorage.getItem("address")) {
        this.address = JSON.parse(sessionStorage.getItem("address"));
      } else {
        const loading = this.$toast.loading({
          duration: 0, // 持续展示 toast
          forbidClick: true // 禁用背景点击
        });
        getAddressList({
          pageNum: 1,
          pageSize: 10
        }).then(res => {
          loading.clear();
          if (res.code == 0) {
            this.address = res.data.list.length ? res.data.list[0] : {};
          } else {
            this.$toast.fail(res.msg);
          }
          
        });
      }
      this.goodList = JSON.parse(sessionStorage.getItem("goodsList"));
      this.goodList.map(item => {
        this.num += item.num;
        this.allPrice += item.num * item.goutPrice;
      });
    }
  },
  mounted() {
    this.loadData();
  }
};
</script>
<style lang="scss" scoped>
.confirmOrder {
  background: #f6f8fa;
  .top {
    background: #fff;
    padding: 10px;
    .title {
      font-size: 18px;
      line-height: 30px;
      font-weight: bold;
    }
    .address {
      margin-top: 10px;
      .label {
        line-height: 20px;
      }
      .value {
        display: flex;
        align-items: center;
        padding: 10px;
        margin-top: 10px;
        background: #f6f8fa;
        .icon {
          width: 20px;
          height: 20px;
          margin-right: 5px;
        }
        .name {
          font-weight: bold;
          font-size: 15px;
          line-height: 30px;
          span {
            float: right;
          }
        }
        .addr {
          color: #999;
          font-size: 13px;
        }
        .text {
          flex: 2;
          font-size: 14px;
          color: #999;
        }
        .icon1 {
          width: 15px;
          height: 15px;
          margin-left: 10px;
        }
      }
    }
  }
  .mid {
    margin-top: 7px;
    padding: 10px;
    background: #fff;
    .name {
      font-weight: bold;
      font-size: 14px;
      margin-bottom: 10px;
      .text {
        font-weight: normal;
        color: #999;
      }
    }
    .list {
      border-bottom: 1pt solid #f7f7f7;
      .item {
        display: flex;
        align-items: center;
        font-size: 13px;
        line-height: 20px;
        padding: 8px 0;
        .shopName {
          flex: 2;
        }
        .num {
          width: 30px;
          text-align: right;
          color: #999;
          margin-left: 10px;
        }
        .price {
          width: 70px;
          text-align: right;
          margin-left: 10px;
        }
      }
    }
    .allPrice {
      font-size: 12px;
      text-align: right;
      margin-top: 10px;
    }
    .remark {
      margin-top: 10px;
      .text {
        font-size: 13px;
      }
      .value {
        .van-cell {
          font-size: 12px;
          background: #f6f8fa;
        }
      }
    }
  }
  .bottom {
    background: #fff;
    position: fixed;
    left: 0;
    bottom: 0;
    padding: 10px 0;
    width: 100vw;
    border-top: 1pt solid #eee9eb;
    box-sizing: border-box;
    .flex {
      display: flex;
      align-items: center;
      padding: 0 10px;
      .icon {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background: #eb514f;
        text-align: center;
        position: relative;
        margin-right: 5px;
        .label {
          position: absolute;
          width: 20px;
          height: 20px;
          line-height: 20px;
          border-radius: 50%;
          background: #feb001;
          color: #fff;
          font-size: 12px;
          right: 0;
          top: 0;
        }
        img {
          width: 30px;
          margin: 10px 0;
        }
      }
      .allPrice {
        flex: 2;
        font-size: 12px;
      }
      .right {
        .btn {
          width: 80px;
          text-align: center;
          height: 40px;
          line-height: 40px;
          border-radius: 5px;
          background: #eb514f;
          color: #fff;
          font-size: 13px;
        }
      }
    }
  }
  .van-popup {
    border-radius: 10px 10px 0 0;
    overflow: hidden;
    .btn {
      margin: 50px 15px 10px;
      background: #eb5252;
      height: 50px;
      line-height: 50px;
      color: #fff;
      text-align: center;
      font-size: 15px;
      border-radius: 50px;
    }
  }

  .popup_wrap {
    background: #fff;
    padding: 20px;
    width: 80vw;
    border-radius: 10px;
  }

  .popup_icon {
    height: 15px;
    img {
      width: 15px;
      height: 15px;
      float: right;
    }
  }

  .popup_content .title {
    font-weight: bold;
    font-size: 20px;
    line-height: 40px;
  }

  .popup_content .title span {
    color: #eb5252;
  }

  .popup_content .text {
    font-size: 15px;
    color: #999;
    line-height: 25px;
    margin: 10px 0;
    text-align: justify;
  }

  .popup_btns {
    display: flex;
    align-items: center;
    margin-top: 20px;
  }

  .popup_btns .popup_btn {
    border: 1pt solid #eee;
    border-radius: 5px;
    height: 40px;
    line-height: 40px;
    font-size: 15px;
    color: #999;
    width: 125px;
    text-align: center;
  }

  .popup_btns .go {
    margin-left: 15px;
    border-color: #eb5252;
    background: #eb5252;
    color: #fff;
    flex: 2;
  }
}
</style>